import {useEffect, useRef} from "react";

const useClickOutside = (action: () => void, isOpened?: boolean) => {

    const wrapperRef = useRef<HTMLDivElement>(null);
    useEffect(() => {
        const handleClickOutside = (event: any) => {
            if (wrapperRef && wrapperRef.current && !wrapperRef.current.contains(event.target) && isOpened)
            {
                console.log("action")
                action();
            }
        };
        document.addEventListener('mousedown', (e) => handleClickOutside(e));
        return document.removeEventListener('mousedown', (e) => handleClickOutside(e));
    }, [action, isOpened]);

    return {wrapperRef};
};

export default useClickOutside;